.topnav {
    overflow: hidden;
    background-color:#2d4059;
  }
  
  .topnav a {
    float: left;
    color: #f4f4f4;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
  }
  
  .topnav a:hover {
    color:#ea5455;
  }
  
  .topnav a svg{
    height: 5px;
    width: 25px;
    fill:white;
  }

  .topnav a svg:hover{
      fill:#ea5455;
  }

  ::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  ::-webkit-scrollbar-thumb {
    background: #2d4059;
  }

  ::-webkit-scrollbar-thumb:hover {
    background: #ea5455;
  }